import React from 'react'
import {
    Card,
    CardHeader,
    CardTitle,
    Table,
    UncontrolledTooltip
  } from "reactstrap"
  import avatar1 from "../../../assets/img/portrait/small/avatar-s-5.jpg"
  import avatar5 from "../../../assets/img/portrait/small/avatar-s-1.jpg"
export default function TodayLastDayReport() {
  return (
    <Card>
    <CardHeader className='card-header'>
      <CardTitle>Today Last Day Report</CardTitle>
      <p class="card-text font-small-2 mr-25 mb-0"> Updated 1 month ago </p>
    </CardHeader>
    <Table
      responsive
      className="dashboard-table table-hover-animation mb-0 mt-1"
    >
      <tbody>
        <tr>
          <td>#879985</td>
          <td>
            <div
              className="bg-success"
              style={{
                height: "10px",
                width: "10px",
                borderRadius: "50%",
                display: "inline-block",
                marginRight: "5px"
              }}
            />
          </td>
          <td className="p-1">
            <ul className="list-unstyled users-list m-0 d-flex">
              <li className="avatar pull-up">
                <img
                  src={avatar1}
                  alt="avatar"
                  height="30"
                  width="30"
                  id="avatar1"
                />
                <UncontrolledTooltip placement="bottom" target="avatar1">
                  Vinnie Mostowy
                </UncontrolledTooltip>
              </li>
            </ul>
          </td>
          <td>Anniston, Alabama</td>
          <td>
            <span>130 km</span>
          </td>
          <td>26/07/2018 </td>
          <td>28/07/2018</td>
        </tr>
        <tr>
          <td>#156897</td>
          <td>
            <div
              className="bg-warning"
              style={{
                height: "10px",
                width: "10px",
                borderRadius: "50%",
                display: "inline-block",
                marginRight: "5px"
              }}
            />
          </td>
          <td className="p-1">
            <ul className="list-unstyled users-list m-0 d-flex">
              <li className="avatar pull-up">
                <img
                  src={avatar5}
                  alt="avatar"
                  height="30"
                  width="30"
                  id="avatar5"
                />
                <UncontrolledTooltip placement="bottom" target="avatar5">
                  Trina Lynes
                </UncontrolledTooltip>
              </li>
            </ul>
          </td>
          <td>Cordova, Alaska </td>
          <td>
            <span>234 km</span>
          </td>
          <td>26/07/2018 </td>
          <td>28/07/2018</td>
        </tr>
      </tbody>
    </Table>
  </Card>
  )
}
